//listener for runtime messages from popup

let startTimestamp = null;
let ratingStatus = 'no'; // no, pending, yes
let totalMatches = 0;
chrome.runtime.onMessage.addListener((message) => {

    if (message.type === 'startRecording') {
        determineRatingStatus();
        setupCapture(); 
    }

    //listen for deletion of single track
    if (message.type === 'deleteSingleTrack') {
        chrome.storage.local.get('shazam_matches', function(result) {
            let currMatchArray = result['shazam_matches'];
            const deleteTarget = currMatchArray.findIndex(
                (item) => item.trackId === message.track
            );
            currMatchArray.splice(deleteTarget, 1);

            chrome.storage.local.set({ shazam_matches: currMatchArray });
            chrome.runtime.sendMessage({ type: 'deleteTrack' });

            //after this need a refresh of the tracklist
        });
    }

    // set the local shazam library to an empty array (effectively deleting it)
    if (message.type === 'deleteEntireLibrary') {
        chrome.storage.local.set({ shazam_matches: [] });
    }
});

let audioContext = null;
let capturedStream = null;    
// recording function
const setupCapture = () => {
    chrome.tabCapture.capture({ audio: true }, (stream) => {
        capturedStream = stream;
        audioContext = new AudioContext();
        const audioSource = audioContext.createMediaStreamSource(stream);

        let recorder = new Recorder(audioSource);
        if (ratingStatus === 'no' || ratingStatus === 'pending')
        {
            startTimestamp = new Date();
        }
        recorder.start();
        //pipe the audio back out to the system as capture is occuring
        audioSource.connect(audioContext.destination);

        // onStopClick:
        const onStopClick = (requestMessage) => {
            if (requestMessage.type === 'stopRecording') {
                stopCapture();
                recorder.stop(true);
            }
        };

        chrome.runtime.onMessage.addListener(onStopClick);

        // stopCapture:
        const stopCapture = () => {
            audioContext.close();
            capturedStream.getAudioTracks()[0].stop();
            chrome.runtime.onMessage.removeListener(onStopClick);
        };
    });
};

const determineRatingStatus = () => {
    startTimestamp = new Date();
    chrome.storage.local.get('shazam_rating', function(result) {
        ratingStatus = result.shazam_rating && result.shazam_rating.is_rated ? result.shazam_rating.is_rated : 'no';            
    });
    chrome.storage.local.get('total_matches', function(result) {
        const totalMatchedNumber = parseInt(result['total_matches'], 10);
        totalMatches = isNaN(totalMatchedNumber)? 0 : totalMatchedNumber;   
    });
}

class Recorder {
    constructor(source) {
        this.context = source.context;
        this.capture3s = null;
        this.capture6s = null;
        this.capture9s = null;
        this.capture12s = null;
        this.attempts = 0;
        this.wasCancelled = false;
        //check in case createScriptProcessor does not exist
        if (this.context.createScriptProcessor == null)
            this.context.createScriptProcessor = this.context.createJavaScriptNode;
        this.input = this.context.createGain();
        source.connect(this.input);
        this.buffer = [];
        this.channelAudioData = [];
        this.recLength = 0;

        const _this = this;
        chrome.storage.local.get('country', function (result) {
            _this.country = result.country;
        });
    }

    mergeBuffers(recBuffers, recLength) {
        let result = new Float32Array(recLength);
        let offset = 0;
        for (let i = 0; i < recBuffers.length; i++) {
            result.set(recBuffers[i], offset);
            offset += recBuffers[i].length;
        }
        return result;
    }

    // sig generation functions
    getSig = (f32Buffer) => {
        const systemSRate = this.context.sampleRate; //this will be needed eventually
        try {
            this.invokeWorker({
                type: 'getSigX',
                audioF32: f32Buffer,
                index: 0,
                sampleRate: systemSRate,
            });
        } catch (error) {
            // console.log("could not invoke worker", error);
        }
    };

    invokeWorker = (data) => {
        const pathname = '/sigx/sigx-worker.js';
        // let worker = new Worker('/src/js/ux/rec/sigx/sigx-worker.js') //working with importScript('sigx.js')
        let worker = new Worker(pathname);
        const _this = this; //global this
        worker.addEventListener(
            'message',
            function(e) {
                switch (e.data.type) {
                    case 'sigready':
                        _this.sigReady(e.data.index, e.data.signatureArray);
                        break;
                    case 'error':
                        _this.errorHandler({ msg: e.data.msg });
                        break;
                    default:
                        _this.errorHandler({ msg: e.data.msg });
                }
            },
            false
        );

        try {
            worker.postMessage(data); // Send data to our worker.
        } catch (error) {
            // console.log(error);
        }
    };

    uuidv4 = () => {
        return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) =>
            (
                c ^
                (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
            ).toString(16)
        );
    };

    Uint8ToBase64(u8Arr) {
        var CHUNK_SIZE = 0x8000; //arbitrary number
        var index = 0;
        var length = u8Arr.length;
        var result = '';
        var slice;
        while (index < length) {
            slice = u8Arr.subarray(index, Math.min(index + CHUNK_SIZE, length));
            result += String.fromCharCode.apply(null, slice);
            index += CHUNK_SIZE;
        }
        return btoa(result);
    }

    sigReady = (index, sigArray) => {
        let uint8array = new Uint8Array(sigArray.buffer);
        const base64 = this.Uint8ToBase64(uint8array);

        // ----------- Test Code for download of signatures ------------ //
        // const reader = new FileReader();
        // reader.onload = function () {
        //   const popup = window.open();
        //   let link = document.createElement("a");
        //   link.setAttribute("href", reader.result);
        //   link.setAttribute("download", `${Date.now()}.sig`);
        //   popup.document.body.appendChild(link);
        //   link.click();
        // };
        // var sigBlob = new Blob([uint8array.buffer], {
        //   type: "audio/vnd.shazam.sig",
        // });
        // reader.readAsDataURL(sigBlob);
        // attempt to make a request to shazam.com
        const _this = this;
        const matchEndpoint = 'https://www.shazam.com/services/webrec/match_extension';
        fetch(matchEndpoint, {
            method: 'POST',
            body: JSON.stringify({
                data: base64,
                sessionId: this.uuidv4(),
                lang: 'en',
                country: this.country || 'US', // defaults to US in cases where langauge isn't here for some reason
            }),
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then((response) => response.json())
            .then((data) => {
                if (data.matches.length > 0 && !this.wasCancelled) {
                    const track = data.matches[0];
                    this.saveMatch(track);
                    this.terminateAndCleanup();
                } else if (_this.attempts === 4) {
                    chrome.runtime.sendMessage({ type: 'tracknomatch' });
                    this.terminateAndCleanup();
                }
            })
            .catch(() => {
                chrome.runtime.sendMessage({ type: 'tracknomatch' });
                this.terminateAndCleanup();
            })
    };

    saveMatch(trackObject) {
        // get the current track object, if empty create it
        // if not empty then append the current match to the array
        trackObject['matchTime'] = Date.now();

        if (!trackObject.metadata.coverart) {
            chrome.storage.local.get('no_cover_url', function(result) {
                const url = result['no_cover_url'];
                trackObject.metadata['coverart'] = url;
            });
        }

        // fetch the track playback URL and append to the match object
        const trackDiscoveryURL = `https://www.shazam.com/discovery/v5/en/US/web/-/track/${trackObject.trackId}?shazamapiversion=v3`;
        const trackDiscovery = fetch(trackDiscoveryURL)
            .then((response) => response.json())
            .then((data) => {
                const hubArray = data.hub && data.hub.actions;

                let uri;
                if (hubArray) {
                    uri = hubArray.find((x) => x.type === 'uri').uri;
                }

                trackObject['playbackURL'] = uri;
                trackObject['weburl'] = `${data.url}?referrer=browserextension` //replace the weburl with a "cleaner" one + referrer
            });

        const tagCountURL = `https://www.shazam.com/services/count/v2/web/track/${trackObject.trackId}`;
        const tagCount = fetch(tagCountURL)
            .then((response) => response.json())
            .then((data) => {
                const tagCount = data.total;
                trackObject['tagCount'] = tagCount;
            });

        Promise.all([trackDiscovery, tagCount]).then(() => {
            chrome.storage.local.get('shazam_matches', function(result) {
                //empty object
                // debugger;
                if (Object.keys(result).length === 0) {
                    chrome.storage.local.set(
                        { shazam_matches: [trackObject] },
                        function() {
                            // console.log("Value is set to " + trackObject);
                        }
                    );
                } else {
                    let currMatchArray = result['shazam_matches'];
                    const dupe = currMatchArray.some(
                        (entry) => entry.trackId === trackObject.trackId
                    );

                    // if a unique track we want to add it to the list of tracks
                    if (!dupe) {
                        currMatchArray.push(trackObject);
                        chrome.storage.local.set({
                            shazam_matches: currMatchArray,
                        });

                        chrome.storage.local.get(
                            'new_matches',
                            function(result) {
                                // add new match to existing new match array
                                const currNewArray = result.new_matches;
                                currNewArray.push(trackObject.trackId);
                                chrome.storage.local.set({
                                    new_matches: currNewArray,
                                });
                            }
                        );
                    }
                    let showRatingModal = false;
                    
                    if (ratingStatus === 'no' || ratingStatus === 'pending') {
                        totalMatches += 1;
                        // Show the rating shazam page and set localStorage shazam_rating.is_rated = 'pending'
                        // if and only if:
                        // 1)the total matches equals to 5, 20, 70, or 120.... 
                        //   OR previous rating page didn't show
                        // 2)the music returns within 5 seconds.
                        if ((ratingStatus === 'no' && (totalMatches === 5 || (totalMatches - 20) % 50 === 0)) || ratingStatus === 'pending') {
                            let foundTrackTimestamp = new Date();
                            let diffTimestamp = foundTrackTimestamp - startTimestamp;
                            if (diffTimestamp <= 5000) {
                                showRatingModal = true;
                                chrome.storage.local.set({shazam_rating : {is_rated : 'pending'}});
                            }
                            else {
                                totalMatches -= 1;
                            }
                        }
                        chrome.storage.local.set({total_matches: totalMatches});
                    }
                    // notify the good folks that we found a match
                    chrome.runtime.sendMessage({
                        type: 'trackmatch',
                        match: trackObject,
                        library: currMatchArray,
                        rating: showRatingModal
                    });
                }
            });
        });
        return trackObject;
    }

    isRecording() {
        return this.processor != null;
    }

    terminateAndCleanup() {
        const intervals = [
            this.capture3s,
            this.capture6s,
            this.capture9s,
            this.capture12s,
        ];

        intervals.forEach((id) => {
            clearTimeout(id);
        });
        capturedStream.getAudioTracks()[0].stop();
        audioContext.close();
        chrome.runtime.sendMessage({ type: 'listeningState', state: false });
    }

    start() {
        if (!this.isRecording()) {
            // const { numChannels, buffer } = this;
            const _this = this; //global this
            this.processor = this.context.createScriptProcessor(4096, 1, 1); //buffer size / input channels / output channels
            this.input.connect(this.processor);
            this.processor.connect(this.context.destination);

            chrome.runtime.sendMessage({ type: 'listeningState', state: true });

            this.processor.onaudioprocess = function(e) {
                let inputBuffer = e.inputBuffer;
                let copy;
                for (
                    let channel = 0;
                    channel < inputBuffer.numberOfChannels;
                    channel++
                ) {
                    let inputData = inputBuffer.getChannelData(channel);
                    _this.recLength += inputBuffer.length;
                    copy = new Float32Array(inputBuffer.length);
                    copy.set(inputData);
                    _this.channelAudioData.push(copy);
                }
            };

            this.wasCancelled = false;

            // automatically send signatures on a timed interval
            this.capture3s = setTimeout(() => {
                this.stop(false);
            }, 3000);

            this.capture6s = setTimeout(() => {
                this.stop(false);
            }, 6000);

            this.capture9s = setTimeout(() => {
                this.stop(false);
            }, 9000);

            this.capture12s = setTimeout(() => {
                this.stop(false);
            }, 12000);
        }
    }

    stop(forcedStop) {
        const captureBufferSnapshot = this.mergeBuffers(
            this.channelAudioData,
            this.recLength
        );

        this.attempts++;

        // sum the contents of the buffer to determine if no audio is being captured
        const bufferSum = captureBufferSnapshot.reduce((pv, cv) => pv + cv, 0);
        this.getSig(captureBufferSnapshot);

        const bufferSumMinThreshold = 0.01;
        const bufferIsZero = (bufferSum === 0);

        if ((Math.abs(bufferSum) < bufferSumMinThreshold) && !bufferIsZero) {
            chrome.runtime.sendMessage({ type: 'low_audio' });
        }

        if (forcedStop) {
            chrome.runtime.sendMessage({ type: 'cancelled' });
            this.wasCancelled = true;
            this.terminateAndCleanup();
        } else if (bufferSum === 0) {
            // can adjust this threshold for silence
            this.terminateAndCleanup();
            chrome.runtime.sendMessage({ type: 'silence' });
        }
    }
}
